import React from 'react'
import PullLoad from 'components/PullLoad'
import {withRouter} from 'react-router-dom'
import connect from 'connect'
@connect
@withRouter
export default class extends React.Component {
    state = {
        keywordList: ['Easy-Mock', 'webpack', 'vue', 'Javascript'],
        pageindex: 1,
        keyword: ''
    }
    getSearchData (keyword) {
        const {getSearchList} = this.props
        const {pageindex} = this.state
        this.setState({
            keyword
        })
        getSearchList({
            keyword,
            pageindex
        })
    }
    handleLoad () {
        const {getSearchList} = this.props
        const {pageindex, keyword} = this.state
        return getSearchList({
            keyword,
            pageindex
        })
    }
    delhistory () {
        const {delhistory, setFooterList, history} = this.props
        this.props.delhistory()
    }
    handleRefresh () {
        const {refreshSearchList} = this.props
        const {pageindex, keyword} = this.state
        return refreshSearchList({
            keyword,
            pageindex
        })
    }
    render () {
        const {keywordList} = this.state
        const {searchList, hasMore, history} = this.props.state.search
        console.log(history)
        let data=['福寿字画山水图','福寿字画山水图']
        return (
            <div className="search-body-wrapper">
                <div className="search-container">
                    {
                        searchList.length === 0 ? (
                            <div className="guess">
                                <span className="tip">搜索记录</span>
                                <div className="search-btn fr" onClick={this.getSearchData.bind(this)}>
                                    <img src={require('../../../images/sy_zpxs_del.png')} alt="LOGO" className="del"  onClick={this.delhistory.bind(this)}/>
                                </div>
                                <ul className="search-menu cf">
                                    {
                                        data.map( (keyword, index) => (
                                            <li className="fl" key={index} onClick={this.getSearchData.bind(this, keyword)}>{keyword}</li>
                                        ))
                                    }
                                </ul>
                            </div>
                        ) : (
                            <PullLoad handleLoad={this.handleLoad.bind(this)} handleRefresh={this.handleRefresh.bind(this)} hasMore={hasMore}>
                                <div className="search-box">
                                   1
                                </div>
                            </PullLoad>
                        )
                    }
                </div>
            </div>
        )
    }
}



